.main-block {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 20px;
    .main-block__sub-select {
        height: 176px;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: 1fr;
        gap: 10px;
        align-items: flex-end;
        @media all and (max-width:380px) {
            & {
                height: 156px;
            }
        }
        @media all and (max-width:320px) {
            & {
                height: 132px;
                gap: 8px;
            }
        }
        .main-block__sub {
            height: 86%;
            display: flex;
            flex-direction: column;
            background: var(--sub-bg);
            text-align: center;
            color: var(--text);
            border-radius: 5px;
            .main-block__save {
                width: 100%;
                height: 25px;
                background: #FCBF59;
                backdrop-filter: blur(9.06094px);
                border-top-left-radius: 5px;
                border-top-right-radius: 5px;
                @media all and (max-width:320px) {
                    & {
                        height: 21px;
                    }
                }
            }
            .main-block__save_90 {
                background: #FCA759;
            }
            .main-block__save-text {
                font-size: 13px;
                line-height: 15px;
                letter-spacing: 0.78px;
                text-transform: uppercase;
                color: #1A1A1A;
                @media all and (max-width:320px) {
                    & {
                        font-size: 11px;
                        vertical-align: text-top;
                    }
                }
            }
            .main-block__sale {
                position: relative;
                width: 100%;
                height: 16px;
                background: #FCA759;
                backdrop-filter: blur(9.06094px);
                border-bottom-left-radius: 5px;
                border-bottom-right-radius: 5px;
                .main-block__sale-text {
                    font-size: 9px;
                    line-height: 15px;
                    letter-spacing: 0.54px;
                    text-transform: uppercase;
                    color: #1A1A1A;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    @media all and (max-width:320px) {
                        & {
                            font-size: 7px;
                        }
                    }
                }
            }
            .main-block__sub-text-wrapper {
                height: 100%;
                padding: 6px 11px 35px 11px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                align-items: center;
                @media all and (max-width:380px) {
                    & {
                        padding: 2px 10px 24px 10px;
                    }
                }
            }
            .main-block__sub-text-wrapper_3 {
                padding-bottom: 19px;
                @media all and (max-width:380px) {
                    & {
                        padding-bottom: 8px;
                    }
                }
            } 
            .main-block__sub-period {
                font-size: 24px;
                line-height: 28px;
                @media all and (max-width:320px) {
                    & {
                        font-size: 20px;
                        line-height: 23px;
                    }
                }
            }
            .main-block__sub-period_1, .main-block__sub-price-total {
                width: 60%;
                @media all and (max-width:380px) {
                    & {
                        width: 80%;
                    }
                }
            }
            .main-block__sub-price-total, .main-block__sub-price-per-month {
                font-size: 13px;
                line-height: 16px;
                letter-spacing: -0.354546px;
                @media all and (max-width:320px) {
                    & {
                        font-size: 11px;
                        line-height: 13px;
                        letter-spacing: -0.3px;
                    }
                }
            }
        }
        .main-block__sub_selected {
            background: linear-gradient(225deg, #894AC9 0%, #4D46B1 100%);
            color: #FFFFFF;
        }
        
        .main-block__sub_12, .main-block__sub_3 {
            height: 100%;
        }
    }
    .main-block__continue-btn {
        width: 100%;
        height: 50px;
        margin-top: 29px;
        background: linear-gradient(225deg, #542285 0%, #222266 100%);
        border-radius: 5px;
        border-style: none;
        @media all and (max-width:380px) {
            & {
                margin-top: 20px;
            }
        }
        
        @media all and (max-width:320px) {
            & {
                margin-top: 13px;
            }
        }
        .main-block__continue-btn-text {
            font-size: 22px;
            line-height: 28px;
            text-align: center;
            letter-spacing: 0.0352px;
            color: #FFFFFF;
        }
    }
    .main-block__info-line {
        font-size: 11px;
        line-height: 15px;
        text-align: center;
        color: var(--infoline);
        mix-blend-mode: normal;
        opacity: 0.4;
        margin-top: 20px;
        @media all and (max-width: 320px) {
            & {
                margin-top: 17px;
            }
        }
    }    
}
