.top-block {
    height: 53%;
    background: linear-gradient(225deg, #894AC9 0%, #4D46B1 100%);
    position: relative;
    @media all and (max-width:380px) {
        & {
            height: 52%;
        }
    }
    @media all and (max-width:320px) {
        & {
            height: 46%;
        }
    }
    .top-block__header {
        position: relative;
        font-size: 34px;
        line-height: 41px;
        text-align: center;
        letter-spacing: 0.37px;
        padding-top: 80px;
        color: #FFFFFF;
        @media all and (max-width:320px) {
            & {
                font-size: 24px;
                letter-spacing: 0.261176px;
                padding-top: 50px;
            }
        }
        .top-block__close-bg {
            width: 32px;
            height: 32px;
            position: absolute;
            top: 50%;
            right: 3%;
            transform: translateY(-50%);
            border-radius: 50%;
            background: rgba(255, 255, 255, 0.527344);
            .top-block__close-cross {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }
        }
    }
    .top-block__pic {
        width: 112px;
        height: 250px;
        position: absolute;
        top: 28%;
        left: 45%;
        z-index: 3;
        filter: drop-shadow(10px 0px 20px rgba(0, 0, 0, 0.2));
        @media all and (max-width:380px) {
            & {
                width: 95px;
                height: 213px;
                top: 30%;
            }
        }
        @media all and (max-width:320px) {
            & {
                width: 53px;
                height: 117px;
                top: 31%;
                left: 50%;
            }
        }
    }
    .top-block__main {
        position: relative;
        width: 100%;
        margin-top: 33px;
        overflow: hidden;
        @media all and (max-width:380px) {
            & {
                margin-top: 20px;
            }
        }
        @media all and (max-width:320px) {
            & {
                margin-top: 0px;
            }
        }
        .top-block__grid {
            width: 100%;
            height: 100%;
            @media all and (max-width:320px) {
                & {
                    display: none;
                }
            }
        }
        .top-block__grid_mini {
            display: none;
            @media all and (max-width:320px) {
                & {
                    display: block;
                }
            }
        }
        .top-block__graph {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            @media all and (max-width:320px) {
                & {
                    display: none;
                }
            }
        }
        .top-block__graph_mini {
            display: none;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            @media all and (max-width:320px) {
                & {
                    display: block;
                }
            }
        }
        .top-block__graph-text {
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            position: absolute;
            height: 100%;
            top: 0;
            left: 0;
            font-size: 12px;
            line-height: 14px;
            color: #FFFFFF;
            margin-left: 20px;
        } 
    }    
}
.top-block__main::after {
    content: "";
    position: absolute;
    top: 0;
    left: -30%;
    width: 200%;
    height: 100%;
    animation-duration: 3s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-name: backgroundMovement;
    animation-timing-function: linear;
    background: linear-gradient(to right, rgba(156, 156, 156, 0) 15%,rgba(247, 243, 243, 0.3) 30%,rgba(156, 156, 156, 0) 35%);
    transform: skewX(45deg);
    background-size: 200%;
}
@keyframes backgroundMovement {
    0% {
        background-position: 100%;
    }
    100% {
        background-position: -100%;
    }
}
